import React, { Component } from "react";
import ButtonList from "./ButtonList";
import Dino from "./dino";

class App extends Component{

    state = {
        dinos : [
            {
                name : "Hungarosaurus",
                image: "https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fpre00.deviantart.net%2F6f12%2Fth%2Fpre%2Fi%2F2018%2F063%2Fa%2F1%2Fhungarosaurus_by_cisiopurple-dc4xtrk.png&f=1&nofb=1",
                selected: false
            },
            {
                name : "Bakonydraco galaczi",
                image: "https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fupload.wikimedia.org%2Fwikipedia%2Fcommons%2F2%2F22%2FBakonydraco1DB.jpg&f=1&nofb=1",
                selected: false
            },
            {
                name : "Tyrannosaurus",
                image: "https://external-content.duckduckgo.com/iu/?u=http%3A%2F%2Fimg1.wikia.nocookie.net%2F__cb20150213222749%2Fdinosaurs%2Fimages%2F0%2F00%2FOutrun-t-rex-orig.jpg&f=1&nofb=1",
                selected: false
            },
            {
                name : "Miragaia",
                image: "https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fi.pinimg.com%2Foriginals%2F6e%2F24%2F8b%2F6e248ba341db83e91cd5ee2978d49259.png&f=1&nofb=1",
                selected: false
            }
        ]
    }

    onClickHandle = (index) => _ => {
        this.setState({
            dinos: this.state.dinos.map(( char, charIndex ) =>
                (( charIndex === index ) ? ({...char, selected: true}) : ({...char, selected: false}))
            )
        }, () => {
            // console.log(this.state)
        });
    }

    render(){
        return(
            <div>
                <ButtonList onClickFunc={this.onClickHandle} dinos={this.state.dinos}/>
                <Dino selectedChar={ this.state.dinos.find(char => char.selected) }/>
            </div>
        );
    }
}

export default App;