import React, { Component } from "react";



class ButtonList extends Component {

    renderButton = (dino, index) => {
        return <button onClick={this.props.onClickFunc(index)} type="button" key={index}>{dino.name}</button>
    }

    render() {
        return (
            <div>
                {this.props.dinos.map(this.renderButton)}
            </div>
        );
    }
}

export default ButtonList;